import type { FC } from 'react';
import { useMemo } from 'react';
import styled from 'styled-components';
import { graphql } from 'gatsby';
import { timeDifference } from '../utils';
import { withCommonLayout } from '../hoc';
import { bulletListStyle } from '../styles';
import { PageProps } from '../types';

interface Post {
  html: string;
}

interface AboutPageData {
  markdownRemark: Post;
}

const LayoutWrapper = styled.div`
  ${bulletListStyle}
`;

// Template for markdown contents
// Docs: https://www.gatsbyjs.com/docs/how-to/routing/adding-markdown-pages
const AboutPage: FC<PageProps<AboutPageData>> = ({ data }) => {
  const { markdownRemark: { html } } = data;

  const myExp = useMemo(() => timeDifference(new Date(), new Date('2015-10-08')), []);

  const htmlWithDynamicYear = useMemo(() => (
    html.replace('{{__YEAR__}}', myExp)
  ), [html, myExp]);

  return (
    <LayoutWrapper dangerouslySetInnerHTML={{ __html: htmlWithDynamicYear }} />
  );
};

export default withCommonLayout(AboutPage);

export const pageQuery = graphql`
  query {
    markdownRemark(fields: {collection: {eq: "about"}}) {
      html
    }
  }
`;
